/***** User Profile style *****/
form {
    margin: auto;
    margin-top: 25px;
    max-width: 750px;
}
#userForm h3 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: blue;
}
fieldset {
    border: none;
    border-top: 1px solid rgb(220, 220, 220);
    border-bottom: 1px solid rgb(220, 220, 220);;
}
input {
    border: none;
    background-color: rgb(234, 234, 234);
    border-radius: 5px;
    padding: 7px;
    margin-top: 5px;
    width: 100%;
}
#profile {
    display: grid;
    margin-bottom: 30px;
    grid-template-columns: 40% 60%;
}

label {
    margin: 15px;
}
#profile img {
    place-self: center;
}
#userPicture {
    grid-column: 1 / 2 ;
    width: 200px;
    height: 200px;
    background-color: rgb(234, 234, 234);;
    border-radius: 50%;
}
.profileData {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
#emailAddress {
    grid-row: 2 / 3;
    grid-column: 1 / 3;
}


@media (max-width: 600px) { 
    form {
        max-width: 500px;
        display: flex;
        flex-direction: column;
        margin-top: 80px;
    }
    #profile {
        display: flex;
        flex-direction: column;
    }
    #profile img {
        margin: 20px;
    }
    #resetPassword {
        width: 50%;
        margin: auto;
    }
}

@media (max-width: 480px) {
    form {
        max-width: 95vw;
        margin-top: 40px;
        padding: 10px;
    }
    #profile {
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }
    #userPicture {
        width: 120px;
        height: 120px;
    }
    .profileData {
        grid-template-columns: 1fr;
        gap: 10px;
    }
    label {
        margin: 8px 0 4px 0;
    }
    input {
        font-size: 1em;
        padding: 10px;
    }
    #resetPassword {
        width: 90%;
        margin: 20px auto;
    }
    #emailAddress {
        grid-column: 1 / 2;
    }
}

#view_profile {
  display: block;
}

#update_profile {
  display: none;
}
